import React from "react"
import { useShoppingCart } from "use-shopping-cart"

export default function CartPage() {
    console.log("Cart Page")
    const { cartDetails, incrementItem, decrementItem, redirectToCheckout } = useShoppingCart();
    return (


        <div>
            <button onClick={() => redirectToCheckout()}>Go To Checkout</button>
            <hr />
            {Object.keys(cartDetails).map((item) => (
                <div>
                    <p> Name : {cartDetails[item].name}</p>
                    <p> Name : {cartDetails[item].price}</p>
                    <p> Name : {cartDetails[item].quantity}</p>
                    <button onClick={() => {
                        incrementItem(cartDetails[item].sku)
                    }}>+</button>
                    <button onClick={() => {
                        decrementItem(cartDetails[item].sku)
                    }}>-</button>
                </div>
            ))}
        </div>

    )
}
